<template>
  <div style="height:calc(100% - 1em);" >
   <div v-if="showInfo" class="info-container">
       <info :row="currentRow" @closeDiv="closeDiv" ref="infoDetail"></info>
    </div>
   <div class="all-container"  v-show="!showInfo">
      <div class="operate-div" >
       <template v-for="(item, $index) in formList">       
         <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" style="float:right"
                  @getInput="getFrom"></inputs>
         <datePicker ref="timer"  v-if="item.type == 'timer'" type='doubleDate'  style="float:right"
                      :dateOpt="item.options" @getDatePicker="getOption"></datePicker> 
          <selects ref="selects"  v-if="item.type == 'select'" :option="item.options" @getSelect="getOption" style="float:right"></selects>                          
       </template>
      </div>
      <div class="list-content " style="height:calc(100% - 0em);">
          <div ref="myOwnTable" class="table-container" style="height: 100%,padding:0; width: 100%; font-size: 0.94em" >
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="clickTableRow"></tables>
          </div>
    
      </div>
    </div>
    <dialogs type="alert" ref="alerts"></dialogs>
    <dialogs type="customer" ref="writeDia" v-if="showWriteDia" width="50em">
      <div slot="header">
         电表手动补录
      </div>
      <div slot="content">
          <table class="form-list table-list" style="margin:0.3em 0 0 0;width:100%">
             <tr class="table-header" >
                <th width="15%" style="border-bottom:0">表号</th>
                <th width="15%" style="border-bottom:0">日期</th>
                <th style="border-bottom:0">总</th>
                <th style="border-bottom:0">尖</th>
                <th style="border-bottom:0">峰</th>
                <th style="border-bottom:0">平</th>
                <th style="border-bottom:0">谷</th>
            </tr>
          </table>
          <VuePerfectScrollbar class="tableBodyCon" v-if="dialogData.length > 0">
             <table class="form-list table-list" style="margin: 0 0 0.3em 0;width:100%">
                <tr v-for="(item, $index) in dialogData">
                  <!-- <td :width="$keyIndex == 'collectDate' || $keyIndex == 'cno' ? '20%' : 'auto'" v-for="(key, $keyIndex) in item">
                     {{$keyIndex == 'cno' && key != '' && key !== null ?  key.substr(2).replace(/^0*/gi, '') : key}}
                  </td> -->
                    <td width="15%" v-if="item.rowSpan" :rowspan="item.rowSpan">{{item.cno.substr(2).replace(/^0*/gi, '')}}</td>
                    <td width="15%" >{{item.collectDate}}</td>
                    <template v-if="item.flag == 1 && juedeEdit('', item, $index)">
                        <th ><input type="text" class="writeInput" maxlength="10"  v-model="item.readValue" @change="getInputData( 'readValue', item, $index)" /></th>
                        <th ><input type="text" class="writeInput" maxlength="10" v-model="item.readValue1" @change="getInputData( 'readValue1', item, $index)" /></th>
                        <th ><input type="text" class="writeInput" maxlength="10" v-model="item.readValue2" @change="getInputData('readValue2', item, $index)" /></th>
                        <th ><input type="text" class="writeInput" maxlength="10" v-model="item.readValue3" @change="getInputData('readValue3', item, $index)" /></th>
                        <th ><input type="text" class="writeInput" maxlength="10" v-model="item.readValue4" @change="getInputData( 'readValue4', item, $index)" /></th>
                    </template>
                    <template v-else>
                        <th >{{item.readValue}}</th>
                        <th >{{item.readValue1}}</th>
                        <th >{{item.readValue2}}</th>
                        <th >{{item.readValue3}}</th>
                        <th >{{item.readValue4}}</th>
                    </template>
                </tr>
             </table>
          </VuePerfectScrollbar>
      </div>
      <div slot="footer">
          <button class="button-default" @click="submitData">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
      </div>
    </dialogs>
  </div> 
</template>

<script>
   import info from './CostDetail.vue'
   export default {
     data() {
       return {
          showInfo: false,
          searchDate: '',
          searchParam: {
            customerName: '',
            propertyName: '',
            customerContact: '',
            customerAddr: '',
            startDate: '',
            endDate: ''
          },
          showWriteDia: false,
          tableShow: false,
          TableOpt: {
              column: [],
              url: 'customerInfo/electConsumption',
              method: 'post',
              pageSize: 20,
              isExport: true,
              exportURL: 'comprehensive/downelectDetailList',
              pageNumber: 1,
              queryParam: {startDate: '', endDate: ''},
              pageList: [20, 40, 60],
              dataRow: 'list',
              showTotal: true, 
              totalOpt: [
                 {title: '用电量', field: 'electUse',  textAlign: 'center', width: '160'},
                 {title: '消费金额', field: 'electCharge', textAlign: 'center', width: '160'}
              ]
            },
          currentRow: '',
          //当前用户的设备补抄数据
          dialogData: []
       }
     },
     components: {info},
     computed: {
       column: function () {
          return [
                {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '160'},
                {title: '门牌编号', field: 'propertyName', textAlign: 'center', width: '160'},
                {title: '用电量', field: 'electUse', textAlign: 'center', width: '160'},
                {title: '消费金额', field: 'electCharge', textAlign: 'center', width: '160'},
                {title: '账户余额', field: 'remainAmount', textAlign: 'center', width: '160'},
                {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '200'},
                {title: '联系电话', field: 'customerContact', textAlign: 'center', width:'140'},
                {title: '部门机构', field: 'orgName', textAlign: 'center', width: '150'},
                {title: '汇总天数', field: 'summryDay', textAlign: 'center', width: '120'},
                {
                  title: '缺失数据天数', 
                  field: 'defectDay', 
                  textAlign: 'center',
                  width: '150',
                  type:"object",
                  formatter: function (val, key, row, obj) {
                     // if(val > 0) {
                     //   return {
                     //      template:  '<div>' + val + '  ' + '<button class="list-operate"  @click="openWriteDia">' + '补录</button></div>',
                     //      methods: {
                     //         openWriteDia: function () {
                     //           obj.$parent.openWriteDia(row);
                     //         }
                     //      }
                     //    }
                     // } else {
                       return {
                         template: '<div>' + val + '</div>'
                     // }
                    }
                  }
                }
              ]
       },
       formList: function () {
         let dates = this.initDate();
     	    return [ 
              {
               type: 'input',
               options: {
                  type: 'typeSearch',
                  key: 'searchParam',
                  value: '',
                  selectOpt: {
                    width: '7em',
                    cs: 'client',
                    data: [
                      {text: '用户姓名', value: 'customerName'},
                      {text: '门牌编号', value: 'propertyName'},
                      {text: '联系电话', value: 'customerContact'},
                      {text: '用户地址', value: 'customerAddr'}
                    ],
                    text: 'text',
                    value: 'value',
                    key: 'searchType',
                    constant: {value: 'customerName'}
                  }
                }
              },
              {
            	  type: 'timer',
               	options: {
            	   	key: 'searchDate',
            	    width: '23em',
                  beforeOnlyDay: this.$store.state.balanceTime !=='' ? this.$store.state.balanceTime : (localStorage.systemConfig ? JSON.parse(localStorage.systemConfig).balanceDate : 27),
            	    value: {startDate: dates.start, endDate: dates.end}
            	  }
              }
           ]
       },
     
     },
      created: function () {
        this.isIn();
        this.getAction();
     },
     mounted: function () {

    },
     methods: {
      init: function () {
        let date = this.initDate();
        this.searchDate = date.start + '至' + date.end;
        this.TableOpt.column = this.column;
        this.TableOpt.isExport = this.controls[6];
        let that = this;
        // window.onresize = function () {
        //   that.windowChange()
        // }
      },
      juedeEdit: function (key, row, index) {
          for(let j = index + 1; j<this.dialogData.length;  j++) {
           let curr = this.dialogData[j];
           if(curr.cno === row.cno && curr.flag != 1 && curr[key] !== '') {
              if(key !== '') {
                return curr[key];
              }
              return true;
           } else if(curr.cno !== row.cno) {
              this.$set(this.dialogData[index], 'flag',0);
              if(key !== '') {
                return '';
              }
              return false
           }
         }
      },
      getInputData: function (key, row, index) {
         //parseFloat(src.replace(/[^\d\.]/g,''));
         let before = 0;
         let after = '';
         let alerts = this.$refs.alerts;
         after = this.juedeEdit(key, row, index);
         if(after == '') {
           this.$set(this.dialogData[index], 'flag',0);
         } else {
           for(let i = index - 1;i>-1; i--) {
             let curr = this.dialogData[i];
             if(curr.cno === row.cno && curr.flag != 1 && curr[key] !== '') {
                before = curr[key];
                break;
             } else if(curr.cno !== row.cno) {
                break;
             }
           }
           let val = parseFloat(row[key].replace(/[^\d\.]/g,''));
           if(val < before || val > after) {
             alerts.show({msg: '输入值的范围在之前和之后召测值之间，即 [' + before + '-' + after + '] 之间'});
             this.$set(this.dialogData[index], key, '');
           } else  {
             if(row.readValue1.toString() != '' && row.readValue2.toString() != '' && row.readValue3.toString()!= '' && row.readValue4.toString() != '') {
                let total = parseFloat(row.readValue1) + parseFloat(row.readValue2) + parseFloat(row.readValue3) + parseFloat(row.readValue4);
                if(row.readValue.toString() == '' ){
                   alerts.show({msg: '总不能为空'});
                } else {
                    if(total != parseFloat(row.readValue)) {
                      alerts.show({msg: '尖峰平谷之和应该等于总的值'});
                      this.$set(this.dialogData[index], key, '');
                   }
                }
             } else {
               this.$set(this.dialogData[index], key, val);
             }
           }
         }
      },
      cancelDialog: function () {
          this.$refs.writeDia.close();
          this.dialogData = [];
          this.showWriteDia = false;
      },
      submitData: function () {
         let arr = [];
         let alerts = this.$refs.alerts;
         for(let i =0; i<this.dialogData.length; i++) {
           let row =  this.dialogData[i];
           if(row.flag == 1) {
             if(!((row.readValue1.toString() != '' && row.readValue2.toString() != '' && row.readValue3.toString()!= '' && row.readValue4.toString() != '') || 
               (row.readValue1.toString() == '' && row.readValue2.toString() == '' && row.readValue3.toString() == '' && row.readValue4.toString() == ''))) {
                  alerts.show({msg: '尖峰平谷之和应该等于总的值'});
                  return 
              } else if((row.readValue1.toString() != '' && row.readValue2.toString() != '' && row.readValue3.toString()!= '' && row.readValue4.toString() != '')){
                 arr.push(row);
              }
           }
         }
         let that = this;
         that.getRequest({
           url: 'replenish/manualRecordData',
           method: 'post',
           param: {customerNo: this.currentRow.customerNo, data:  arr},
           success: function(data) {
             alerts.show({msg: data.message});
             that.cancelDialog();
             that.getSearchTable();
           },
           error: function(data) {
              alerts.show({msg: data.message})
           }
         })
         
      },
      //打开补录数据的弹出框
      openWriteDia: function (row) {
        this.currentRow = row;
        let that = this;
        let alerts = this.$refs.alerts;
        that.getRequest({
          //url: 'dist/json/measureData.json',
          //method: 'get',
           method: 'post',
           url: 'replenish/queryData',
          param: {customerNo: row.customerNo, startDate: that.searchParam.startDate, endDate: that.searchParam.endDate},
          success: function (data) {
             let Data = data.data;
             let curr = Data[0].cno;
             let arrIndex = [];
             let arrNum = [];
             let len = 0;
             let last = 0;
             for(let i = 0; i < Data.length; i++) {
                if(i + 1 < Data.length ) {
                  if(curr ===  Data[i].cno) {
                    len = len + 1 ;
                  }
                  if(curr !==  Data[i + 1].cno) {
                     that.$set(Data[last], 'rowSpan', len);
                     last = i+1;
                     curr =  Data[i + 1].cno ;
                     len = 0;
                  }
                } else {
                  if(curr ===  Data[i].cno) {
                    len = len + 1 ;
                  } else {
                    len = 1;
                    last = i;
                  }
                  that.$set(Data[last], 'rowSpan', len);
                } 
             }
             that.dialogData = Data;
             that.showWriteDia = true;
             that.$nextTick(function () {
               that.$refs.writeDia.show();
             })
          },
          error: function(data) {
            alerts.show({msg: data.message})
          }
        })
      },
      //返回到列表
      closeDiv: function () {
              this.showInfo = false;
              this.getSearchTable();
      },
      resizeCon: function () {
          let that = this;
            if (that.$refs.myTable) {
              that.$refs.myTable.onResize();
            }
            if (that.$refs.infoDetail) {

              let info = that.$refs.infoDetail;
              if (info.$refs.myTable){
                  info.$refs.myTable.resizeTable(info.$refs.myOwnTable.width, info.$refs.myOwnTable.clientHeight - 50);
              }
            }
      },
      windowChange: function () {
           let that = this;
           this.resizeCon();
            // window.onresize = function () {
            //    that.resizeCon();
            // }
      },
      getFrom: function (val) {
         let key=val.key.split('.')[1];
         if (this.searchParam[key] !== val.val) {
            this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'startDate', 'endDate']);
            this.getOption(val, true);
            this.getSearchTable();
          }
       },
       getSearchTable: function () {
           let arr = this.getSearchData(this.searchParam);
           this.$refs.myTable.resetTableOpt({column: this.column, isLoadData: false});
           this.searchTable(arr, 1);
        },
       clickTableRow: function (row) {
         this.currentRow = row.val;
         this.showInfo = true;
       } 
      },
     
      watch: {
      	'searchDate': {
      		handler(val, oldVal) {
      		   let arr = this.searchDate.split('至');
      		   if(this.tableShow) {
      		    this.searchParam.startDate = arr[0];
      		    this.searchParam.endDate  = arr[1];
      		   	this.getSearchTable();
      		   	} else {
      		   	   this.searchParam.startDate = this.TableOpt.queryParam.startDate = arr[0];
      		       this.searchParam.endDate = this.TableOpt.queryParam.endDate = arr[1];
      		       this.tableShow = true;
      		   	}
      		}
      	}
      }
   }
</script>

<style lang="scss">
.specailTimer{
		float:right!important;
		.date-button {
          line-height: 1.7
		}
}
.tableBodyCon{
  max-height:300px;
  width:100%;
  position:relative;
  float:left;
  box-shadow: 0 1px 3px rgba(255,255,255,0.1)
}
.table-list {
  .writeInput {
     background: #4D5F77;
     height: 2.2em;
     text-align:center;
     width: 100%;
     padding:0.4em;
     border-color: transparent;
     border-radius:0.2em!important
  }
  .writeInput:hover{
    border-color:  #416CCD
  }

  .hasBackTr{
    background: #15487D
  }
  
}

</style>


