<template>
  <div class="info-div" style="height: 100%;">
    <div class="info-header">
      <span class="callback" @click="resetComponent">电量电费</span>
      <span> > 列表详情</span>
    </div>
    <div class="info-container">
      <div class="list-content  list-contentB">
        <div class="contentL">
          <p class="HF-p">
            <span>当前账户余额</span>
            <i class="hasIcon hasImg" @click="getTotalAmount(true)" style="margin-right:0">
              <img src="dist/img/costControl/fresh.png" style="height:auto"/>
            </i>
          </p>
          <p class="content-p">
            <span>{{ typeof amountData.remainAmount == 'undefined' ? '--' : amountData.remainAmount}}</span>元
          </p>
        </div>
        <div class="contentR">
          <ul class="form-list bottomBorderList">
            <li v-for="(item, $index) in customerList">
              <span class="label-span">{{item.name}}</span>
              <span class="label-show " style="text-align: left"> ： &nbsp; {{item.value}}</span>
            </li>
          </ul>
          <div class="labelDiv">
            <div class="labelDivL">
              <img class="big" src="dist/img/measure/elect_thismonth.png"/>
            </div>
            <div class="labelDivR">
              <span class="top colorY">{{typeof amountData.currentMonthTotal == 'undefined' ? '&#45;&#45;' : amountData.currentMonthTotal}}</span>
              <span>本月电量</span>
            </div>
          </div>
          <div class="labelDiv">
            <div class="labelDivL">
              <img class="small" src="dist/img/measure/money.png"/>
            </div>
            <div class="labelDivR">
              <span class="top colorO">{{typeof amountData.currentMonthFee == 'undefined' ? 'a' : amountData.currentMonthFee}}</span>
              <span>本月电费</span>
            </div>
          </div>
      
        </div>
      </div>
      <div class="list-content list-contentB hasOperateNoB" >
        <div class="list-header ">
          <i class="hasIcon hasImg">
            <img src="dist/img/analyse/Record_of_deductions.png"/>
          </i> &nbsp;扣费记录
        </div>
        <div class="operate-div">
          <template v-for="(item, $index) in formList">
            <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" style="float:right"
                    @getInput="getFrom"></inputs>
            <datePicker ref="timer" v-if="item.type == 'timer'" type='doubleDate' :dateOpt="item.options"
                        @getDatePicker="getOption"></datePicker>
          </template>
        </div>
        <div ref="myOwnTable" class="table-container" style="height: calc(100% - 4em);font-size:0.94em">
          <tables ref="myTable" v-if="showTable" :tableOpt="TableOpt" class="tablePadding"></tables>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        searchDate: '',
        showTable: false,
        amountData: {
          remainAmount: '',
          currentMonthTotal: '',
          currentMonthFee: ''
        },
        TableOpt: {
          column: [
            {title: '扣费时间', field: 'feePayTime', textAlign: 'center', width: '160'},
            {title: '用户表号', field: 'deviceNo', textAlign: 'center', width: '160'},
            {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '160'},
            {title: '起度', field: 'startDegree', textAlign: 'center', width: '140'},
            {title: '止度', field: 'endDegree', textAlign: 'center', width: '150'},
            {title: '电价', field: 'price', textAlign: 'center', width: '140'},
            {title: '倍率', field: 'ratio', textAlign: 'center', width: '100'},
            {title: '电量', field: 'electricTotal', textAlign: 'center', width: '150'},
            {title: '扣费金额', field: 'feePayMoney', textAlign: 'center', width: '150'},
            {title: '账户余额', field: 'remainAmount', textAlign: 'center', width: '160'},
            {title: '扣费方式', field: 'feePayType', textAlign: 'center', width: '200'}
          ],
          url: 'comprehensive/electDetailList',
          method: 'post',
          pageSize: 20,
          pageNumber: 1,
          queryParam: {customerNo:'',startDate: '', endDate: ''},
          pageList: [20, 40, 60],
          showTotal: true,
          dataRow: 'list',
          totalOpt: [
            {title: '用电量', field: 'electricTotal', textAlign: 'center', width: '150'},
            {title: '消费金额', field: 'feePayMoney', textAlign: 'center', width: '150'}
          ]
        },
        searchParam: {
          customerNo: '',
          deviceNo: '',
          meterUserNo: '',
          startDate: '',
          endDate: ''
        }
      }
    },
    props: ['row'],
    methods: {
      resetComponent: function () {
        this.$emit('closeDiv');
      },
      getFrom: function (val) {
        let key = val.key.split('.')[1];
        if (this.searchParam[key] !== val.val) {
          this.searchParam = this.cleanSearchData(this.searchParam, ['customerNo','startDate', 'endDate']);
          this.getOption(val, true);
          this.getSearchTable();
        }
      },
      getSearchTable: function () {
        let arr = this.getSearchData(this.searchParam);
        this.searchTable(arr, 1);
      },
      getTotalAmount: function (flag) {
        let that = this;
        that.getRequest({
          url: 'comprehensive/electDetail',
          method: 'post',
          param: {customerNo: that.row.customerNo},
          success: function (data) {

            for (let key in data.data) {
              that.$set(that.amountData, key, data.data[key]);
            }
            if(flag) {
               that.$refs.alerts.show({msg: '刷新成功！'})
            }
          }
        }, true)
      }
    },
    computed: {
      customerList: function () {
        return [
          {name: '用户姓名', value: this.row.customerName || ''},
          {name: '用电类别', value: this.row.customerName || ''},
          {name: '门牌编号', value: this.row.propertyName || ''},
          {name: '联系方式', value: this.row.customerContact || ''},
          {name: '用户地址', value: this.row.customerAddr || ''}
        ]
      },
      formList: function () {
        let dates = this.initDate();
        return [
          {
            type: 'input',
            options: {
              type: 'typeSearch',
              key: 'searchParam',
              value: '',
              selectOpt: {
                width: '7em',
                cs: 'client',
                data: [
                  {text: '用户表号', value: 'deviceNo'},
                  {text: '表计户号', value: 'meterUserNo'}
                ],
              }
            }
          },
          {
            type: 'timer',
            options: {
              key: 'searchDate',
              width: '23em',
              value: {startDate: dates.start, endDate: dates.end}
            }
          }
        ]
      }
    },
    mounted:function(){
      this.getTotalAmount(); //读取汇总信息
    },
    created: function () {
      let date = this.initDate();
      this.searchDate = date.start + '至' + date.end;

    },
    watch: {
      'searchDate': {
        handler(val, oldVal) {
          let arr = this.searchDate.split('至');
          if (this.showTable) {
            this.searchParam.customerNo= this.row.customerNo;
            this.searchParam.startDate = arr[0];
            this.searchParam.endDate = arr[1];

            this.getSearchTable();

          } else {
            this.searchParam.customerNo= this.row.customerNo;
            this.TableOpt.queryParam.customerNo=this.row.customerNo;
            this.searchParam.startDate = this.TableOpt.queryParam.startDate = arr[0];
            this.searchParam.endDate = this.TableOpt.queryParam.endDate = arr[1];
            this.showTable = true;
          }
        }
      }
    }
  }
</script>
<style lang="scss">
  $leftContentW: 21%;
  $leftContentH: 1em;
  .contentL {
    width: $leftContentW;
    font-size: 0.83em;
    margin: 0.5em 1.3em;
    padding: 1.25em;
    min-height: $leftContentH;
    border: 0.2em solid #3E8BDB;
    border-radius: 0.4em;
    float: left;
    p.HF-p {
      font-size: 1em;
      text-align: left;
      margin: 0;
      i.hasIcon {
        float: right;
        color: #3E8BDB;
        margin-right: 0.5em;
        cursor: pointer;
      }
      i.hasIcon:hover {
        color: white
      }
    }
    p.content-p {
      margin: 2.5em 2em;
      text-align: center;
      span {
        font-size: 2.7em;
        color: #EE8836;
        margin-right: 0.3em;
        font-weight: 600;
      }
    }
  }

  .contentR {
    float: left;
    margin: 1em 1.5em 0 1.5em;
    width: calc(100% - #{$leftContentW} - 9.9em);
    min-height: calc(#{$leftContentH} + 0.4em + 2.5em);
    .form-list {
      li {
        width: calc(30% - 2em);
        font-size: 0.87em;
        margin: 0 1em 0.1em 0em;
        .label-show {

          min-width: 7em
        }
        .operate-div {
          padding: 0;
          margin-left: 2.5em
        }
      }
    }
    .bottomBorderList {
      padding-bottom: 0.2em;
      margin-bottom: 1em;
      font-size: 1.1em;
      border-bottom: 1px solid rgb(88, 100, 116);
    }
    .labelDiv {
      min-width: 8em;
      height: 3.8em;
      paddding: 0 1em;
      float: left;
      margin: 0 2em;
      position: relative;
      .labelDivL {
        position: absolute;
        width: 2.5em;
        height: 100%;
        left: 0;
        top: 0;
        .big {
          width: 2.471em;
          height: 2.471em;
          margin-top: 0.6em
        }
        .small {
          width: 2.118em;
          height: 2.118em;
          margin-top: 0.8em
        }
      }
      .labelDivR {
        position: absolute;
        left: 2.5em;
        top: 0;
        height: 100%;
        span {
          display: block;
          min-width: 5em;
          font-size: 0.9em;
          font-weight: 100;
          line-height: 1.3
        }
        span.top {
          font-size: 1.2em;
          margin-top: 0.3em;
          font-weight: 600;
        }
        span.colorY {
          color: #F5C537
        }
        span.colorO {
          color: #DE8845
        }
        span.colorR {
          color: #E4585A
        }
      }
    }
  }

  .hasOperateNoB {
     padding: 1em 1em 0  !important;
     height:calc(100% - 14.5em);
     width:calc(100% - 2em);
    .list-header {
      border-bottom: 0;
   
      width: fit-content;
      float: left;
    }
    .operate-div {
      padding: 0;
      font-size: 0.94em
    }
  }
</style>



